html,
body {
  height: 100%;
}

.app {
  width: 900px;
  background-color: #efefef;
  margin: auto;
  box-shadow: 5px 5px 10px #bdc3c7;
}

@media screen and (max-width: 900px) {
  html,
  body {
    background-color: #efefef;
  }
  .app {
    padding-top: 40px;
    border: none;
    width: 100%;
    box-shadow: none;
    margin: 0;
  }
  header {
    position: fixed;
    top: 0;
    z-index: 2;
    width: 100%;
  }
}

header {
  background-color: #ff6600;
  color: #212121;
  padding: 10px 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

header h1 {
  color: #212121;
  font-size: 22px;
  font-weight: bold;
  margin: 0;
  flex-grow: 2;
}

header .icon {
  width: 25px;
  height: 25px;
  display: block;
  background-repeat: no-repeat;
}

header .back,
header .about {
  background-color: #212121;
  mask-repeat: no-repeat;
}

header .back {
  margin-right: 10px;
}

article {
  padding: 20px;
}

.camera {
  margin: 40px 0;
}

.camera #camera, .camera label {
  margin: 0 auto;
  display: block;
}

.camera #camera {
  width: 75px;
  height: 75px;
}

.camera label {
  text-align: center;
  font-size: 22px;
  font-weight: bold;
  width: 300px;
  margin-bottom: 10px;
}

.camera video {
  width: 100%;
}

.camera #capture-button {
  background: #212121;
  color: #fcfcfc;
  border-radius: 5px;
  border: 0;
  padding: 10px 20px;
  font-size: 22px;
  margin: 10px auto;
  display: none;
}

.camera .display {
  display: block !important;
}

.camera canvas {
  width: 100%;
}

.login label {
  display: none;
}

.login input[type="email"], .login input[type="password"] {
  font-size: 16px;
  width: 400px;
  background-position: -400px 0;
  background-size: 400px 100%;
}

@media screen and (max-width: 900px) {
  input[type="email"], input[type="password"] {
    width: 100% !important;
  }
}

.file .captureInput {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.file label {
  width: 75px;
  height: 75px;
  display: block;
  margin: 20px auto;
}

.file button {
  margin: 10px auto;
  display: none;
}

.file img {
  width: 100%;
}

.file .display {
  display: block !important;
}

.file p {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
}

#snackbar {
  visibility: hidden;
  /* Hidden by default. Visible on click */
  min-width: 350px;
  margin-left: -175px;
  background-color: #212121;
  color: #fcfcfc;
  text-align: center;
  border-radius: 4px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  left: 50%;
  bottom: 30px;
}

#snackbar.show {
  visibility: visible;
  /* Show the snackbar */
  /* Add animation: Take 0.5 seconds to fade in and out the snackbar.
  However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */
@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

#camera, #capture-label {
  background-image: url("../assets/img/camera.png");
}



